.discountSliderContainer{
    background-color: rgb(61, 90, 72);
    padding: 10px 20px;
    border-radius: 8px;
    display:flex;
    flex-direction:column;
    justify-content: center;

    .discountSlider{
        border-radius: 8px;
        max-height: 500px;
        max-width: 90vw;
        box-sizing: border-box;
        min-width: 90%;
        min-height: 50%;

        .splide__arrows{
            .splide__arrow.splide__arrow--prev{
                background-color: rgb(159, 207, 163);
                border-radius:0 8px 8px 0;
                min-width:30px;
                max-width: 30px;
                height: 50px;
                left:-25px;
                box-shadow: 0 1px 2px #000000;
            }

            .splide__arrow.splide__arrow--next{
                background-color: rgb(159, 207, 163);
                border-radius: 8px 0 0 8px;
                min-width:30px;
                max-width: 30px;
                height: 50px;
                right:-25px;
                box-shadow: 0 1px 2px #000000;
            }
            
            
        }
        @media only screen and (min-width: 1200px) {
            .splide__arrows{
            .splide__arrow.splide__arrow--prev{
                // background-color: blue;
                left: 60%;
                top:98%;
                border-radius: 8px;
                min-width:40px;
                max-width: 40px;
                height:18px

            }
            .splide__arrow.splide__arrow--next{
                // background-color: blue;
                left: 40%;
                top:98%;
                border-radius: 8px;
                min-width:40px;
                max-width: 40px;
                height:18px;
            }
        }
        }

        @include media-breakpoint-up(lg) {
            // min-height: 500px;
            max-height: 500px;
        }
    }
}
