.specialOffer{
    position:relative;
    background-color: azure;
    border-radius: 0 0 8px 8px;

    .slider{
        width:100%;
        height:200px;
        box-shadow: 0 1px 2px #000;
        border-radius: 8px;
    }

    .offers{
        height:200px;
        background-color: rgb(211, 193, 234);
        border-radius: 0 0 8px 8px;

    }

    p,span{
        font-family: 'IRANYekan';
        color: rgb(0, 0, 0);
    }


    .header{
        max-height: 40px;
        border-bottom: 2px solid rgb(0, 0, 0);
        padding: 7px 0;
        margin : 0 7px;
    }


    .splide__arrows{
        .splide__arrow.splide__arrow--prev{
            background-color: burlywood;
            border-radius:0 8px 8px 0;
            min-width:20px;
            max-width: 20px;
            height: 50px;
            left:0px;
            box-shadow: 0 1px 2px #000000;
        }

        .splide__arrow.splide__arrow--next{
            background-color: burlywood;
            border-radius: 8px 0 0 8px;
            min-width:20px;
            max-width: 20px;
            height: 50px;
            right:0px;
            box-shadow: 0 1px 2px #000000;
        }
        
    }
}