.filter{
    align-items: center;
    justify-content: flex-start;
    direction : rtl;

    .filterIcon{
        border-bottom: 1px solid black;
        justify-content: center;
        align-items: center;
        position: relative;

        &:hover{
            color:rgba(0,0,0,1);
            cursor: pointer;
        }

        &:before{
            content: '';
            position: absolute;
            width: 0;
            height: 2px;
            bottom: -1px;
            left: 50%;
            transform: translate(-50%,0%);
            background-color: rgb(255, 123, 195);
            visibility: hidden;
            transition: all 0.3s ease-in-out;
          }

          &:hover:before{
            visibility: visible;
            width: 100%;
          }
    }
    p{
        font-family: 'IRANYekan';
    }
}