.slideShow{
    box-shadow: 0px 1px 2px #000000;
    border-radius: 8px;
    // margin: 0 20px;
    // min-height: 500px;
    justify-content: center;
    align-items: center;

    .splide__pagination__page{
        background-color: rgb(255, 255, 255);
    }

    .splide__pagination__page.is-active{
        background-color: rgb(0, 0, 0);

    }
    
    // max-height: 465px;
    img{
        width:100%;
        height:auto;
        border-radius: 8px;
    }

    @include media-breakpoint-up(xl) {
        max-height: 500px;
        min-height: 500px;
        max-width: 1000px;
        min-width: 1000px;
        img{
            max-height: 500px;
            min-height: 500px;
            max-width: 1000px;
            min-width: 1000px;
        }
    }

    @include media-breakpoint-down(lg) {
        max-height: 500px;
        min-height: 500px;
        max-width: 1000px;
        min-width: 800px;
        img{
            max-height: 500px;
            min-height: 500px;
            max-width: 1000px;
            min-width: 800px;
        }
    }

    @include media-breakpoint-down(md) {
        margin: 10px 0;
        max-width: 100vw;
        min-width: 70vw;
        min-height: auto;
        max-height: 500px;
        height: auto;
        img{
            max-height: 500px;
            min-height: auto;
            max-width: 100vw;
            min-width: 70vw;
        }
        // min-height: 300px;
    }
    .slideTitle{
        position: absolute;
        top:7em;
        left:5em;
        background-color: rgba(255, 255, 255, 0.2);
        border-radius: 5px;
        color: #333;
        justify-content: space-around;
        align-items: center;
        padding: 10px;

        h6{
            color:rgb(137, 158, 19);
            font-weight: 500;
        }
        p{
            color:white;
        }
        @include media-breakpoint-down(md) {
            top:3em;
            left:3em;
            h6{
                font-size: 2vw;
            }
            p{
                font-size: 2vw;
            }
        }
    }

    @include media-breakpoint-down(xs) {
        margin-top: 80px;
    }

    @supports (-webkit-backdrop-filter: none) or (backdrop-filter: none) {
        .slideTitle {
          -webkit-backdrop-filter: blur(3px);
          backdrop-filter: blur(3px);
          background-color: rgba(31, 109, 7, 0.4);  
        }
      }
}