@import url('https://fonts.googleapis.com/css?family=Josefin+Sans');

.shareModal{
    justify-content: center;
    align-items:center;
    position: absolute;
    top: 40%;
    right: 0;
    transform: translate(-50%, -50%);
    width: 200px;
    height: 200px !important;
    background-color: rgb(255, 255, 255);
    // border: 2px solid #000;
    box-shadow: 0px 1px 2px #000;
    border: none !important;
    outline: none !important;
    direction:rtl;
    padding:5px 10px;
    box-sizing: border-box;

    .closeIcon{
        position: absolute;
        left:10px;
        top:10px;
        width:20px;
        height:20px;
        padding:2px;
        &:hover{
            color:rgb(92, 92, 92);
            cursor:pointer;
        }
    }

    .notifyCheckContainer{
        p,span,label{
            font-size: 10px;
        }
    }
}

.productCartView{
    position: relative;
    direction: rtl;
    justify-content: flex-end;

    p,h4,h5,h6,span{
        font-family: 'IRANYekan';
    }

    .cartSideDesign{
        position: absolute;
        background: linear-gradient(0deg, rgba(5,45,9,1) 0%, rgb(125, 207, 98) 100%);
        min-height: 100%;
        min-width: 50%;
        right:0;
        z-index: 0;
        border-radius: 8px;
        box-shadow: 0px 1px 2px #555,0px -1px 2px #555;

        @include media-breakpoint-down(xs) {
            min-width: 30%;
        }

        .shareIcon{
            justify-content: center;
            align-items: center;
            width:50px;
            height:50px;
            position:absolute;
            right:0;
            top:0;

            svg{
                color: rgb(41, 41, 41);
                transition: color .8s;

            &:hover{
                color: rgb(111, 111, 111);
                cursor: pointer;
                transition: color .8s;
            }
            }
        }

        

        .notifyIcon{
            justify-content: center;
            align-items: center;
            width:50px;
            height:50px;
            position:absolute;
            right:50px;
            top:0;

            svg{
                color: rgb(41, 41, 41);
                transition: color .8s;

            &:hover{
                color: rgb(111, 111, 111);
                cursor: pointer;
                transition: color .8s;
            }
            }
            
        }

        .logo{
            position: absolute;
            right: 20px;
            width: 50px;
            height: 100%;

            h3{
                font-family: 'Josefin Sans', cursive;
                letter-spacing: 5px;
                transform: rotate(90deg);
                transform-origin: center;
                background: -webkit-linear-gradient(45deg,rgb(12, 80, 12), rgb(62, 160, 49));
                background-clip: text;
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
            }

            @include media-breakpoint-down(xs) {
               right: 5px;
               width: 25px;
               h3{
                   font-size: 20px;
               }
            }
        }

    }

    .cartContainer{
        position: relative;
        background-color: rgb(255, 255, 255);
        border-radius: 8px;
        box-shadow: 2px -1px 2px rgb(94, 94, 94), -1px 1px 2px rgb(133, 133, 133);
        z-index: 1;
        margin: 40px 0;

        .rateContainer{
            align-items: center;
            direction: ltr;
            position: absolute;
            right:0;
            width: 50px;
            height: 100%;

            .rate{
                transform:rotate(90deg);
                transform-origin: center;

                @include media-breakpoint-down(xs) {
                    transform: none;
                }
            }

            @include media-breakpoint-down(xs) {
                align-items: flex-start;
                width: 100%;
                height: 50px;
            }
        }

        .status{
            justify-content: center;
            align-items: center;
            position: absolute;
            right:0;
            top:0;
            width: 60px;
            height:40px;
            border-radius: 0 8px 0 12px;
            box-shadow: 0 1px 2px rgb(40, 40, 40);
            background-color: white;
            box-sizing: border-box;

            p{
                font-family: 'IRANYekan';
                color: red;
            }
        }      
    

        .productImage{
            padding: 30px;
            box-sizing: border-box;
            // background-color: blue;
            min-width: 200px;
            min-height: 200px;
            justify-content: center;
            align-items: center;
            img{
                width: 100%;
                height: auto;
                max-width: 400px;
                max-height: 400px;
            }
        }

        .productInfo{
            padding: 40px 0px 40px 20px;
            box-sizing: border-box;
            justify-content: center;

            @include media-breakpoint-down(xs) {
                padding-right:30px;
            }

            .oldPrice{
                position: relative;
                direction: rtl;
                color:rgb(118, 118, 118);

                &::before{
                    content: '';
                    width: 100%;
                    position: absolute;
                    right: 0;
                    top: 28%;
                    border-bottom: 1px solid rgba(60, 60, 60, 0.8);

                    @include media-breakpoint-down(xs) {
                       top:42%;
                    }
                }
            }

            .button{
                background-color: red;
                width:100%;
                transition: background .5s;

                &:hover{
                    background-color: rgb(255, 98, 98);
                }
                .miniBtn{
                    position:absolute;
                    color:rgb(43, 43, 43);
                    cursor: pointer;
                    transition: color 0.5s;
                    &:hover{
                        color:rgb(227, 227, 227);
                        
                    }
                }
            }

            p,h4,h6,span{
                font-family: 'IRANYekan';
            }

            p{
                color:rgb(40, 40, 40);
                padding:10px 0;
            }
        }
    }
    
}