.category{

    .main{
        display: flex;
        width: 100%;
        height: fit-content;
        justify-content: flex-end;
        align-items: center;
       

        .title{
            display: flex;
            justify-content: flex-end;
            align-items: center;
            position: relative;
            width: 90%;
            height:30px;
            text-align: right;
            
            font-family: 'IRANYekan';
        }

        .bold{
            font-weight: bold;
        }

        .bolder{
            font-weight: bolder;
        }

        .link{

            &:hover{
                color:rgba(0,0,0,.5);
                cursor: pointer;
            }
        }

        .icon{
            display: flex;
            justify-content: center;
            align-items: center;
            position: relative;
            width: 10%;
            height:30px;
            overflow: hidden;
            text-overflow: clip;
            white-space: nowrap;

            &:hover{
                svg{
                    fill: rgb(173, 156, 185);
                }
                cursor:pointer;
            }

            
        }

        .hideText{
            position: relative;
            overflow: hidden;
            text-overflow: clip;
            white-space: nowrap;
            transition: all .5s cubic-bezier(0.77, 0, 0.175, 1);
        }

       .rotateDown{
            svg{
                transform: rotate(-90deg);
                transition: all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
            }

        }

        .rotateUp{
            svg{
                transform: rotate(180deg);
                transition: all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
            }

        }
    }

}