.lang{
    justify-content: center;
    align-items: center;
    .langBox{
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        min-width: 30px !important;
        max-width: 30px !important;
        height: 30px;
        border-radius: 50%;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        cursor: pointer;

        &:hover{box-shadow: 0px 1px 2px #727070;}
    }

    @include media-breakpoint-down(xs) {
        padding:0 20px;
        justify-content: flex-end;
    }

    .animDiv{
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        height: 30px;

        img{
            width: 30x;
            height: 30px;
            padding:0 10px;
        }
    }
}