.sort{
    align-items: center;
    justify-content: center;
    border-bottom: 1px solid black;
    margin-left:15px !important;

    .sortIcon{
        position: relative;

        @include media-breakpoint-down(sm) {

            &:hover{
                color:rgba(0,0,0,1);
                cursor: pointer;
            }

            &:before{
                content: '';
                position: absolute;
                width: 0;
                height: 2px;
                bottom: -1px;
                left: 50%;
                transform: translate(-50%,0%);
                background-color: rgb(255, 123, 195);
                visibility: hidden;
                transition: all 0.3s ease-in-out;
            }

            &:hover:before{
                visibility: visible;
                width: 100%;
            }
        }
    }

    p{
        font-family: 'IRANYekan';
    }

    .sortGroup{
        display: flex;
        position:relative;
        justify-content: flex-end;
        align-items: center;
        width:80%;
        height:50px;
        padding:0 10px;
        box-sizing: border-box;
        direction: rtl;

        @include media-breakpoint-down(md) {
            width: 90%;
        }

        @include media-breakpoint-down(sm) {
            width:100%;
            height: auto;
            flex-wrap: wrap;
            background-color: whitesmoke;
            box-shadow: 0 1px 3px #444;
            z-index: 10;
        }


        li{
            position: relative;
            flex:1 1 auto;
            justify-content: center;
            text-align: center;
            align-items: center;
            font-family: 'IRANYekan';
            padding: 3px 5px 5px 5px;
            box-sizing: border-box;
            list-style-type: none;
            color:rgba(0,0,0,0.6);

            @include media-breakpoint-down(sm) {
                min-width: 100%;
            }

            &:hover{
                color:rgba(0,0,0,1);
                cursor: pointer;
            }

            &:before{
                content: '';
                position: absolute;
                width: 0;
                height: 3px;
                bottom: -10px;
                left: 50%;
                transform: translate(-50%,0%);
                background-color: rgb(255, 123, 195);
                visibility: hidden;
                transition: all 0.3s ease-in-out;

                @include media-breakpoint-down(sm) {
                    bottom: -1px;
                }
              }

              &:hover:before{
                visibility: visible;
                width: 100%;
              }

            
        }
    }



    .mostVisited{
        li:nth-child(1){
            position: relative;
            background-color: rgb(40, 40, 40);
            color:whitesmoke;
            border-radius: 10px;
            box-shadow: 0 1px 2px #000000;
            
        }
    }

    .mostSelling{
        li:nth-child(2){
            position: relative;
            background-color: rgb(0, 0, 0);
            color:whitesmoke;
            border-radius: 10px;
            box-shadow: 0 1px 2px #000000;
        }
    }

    .mostFavorited{
        li:nth-child(3){
            position: relative;
            background-color: rgb(0, 0, 0);
            color:whitesmoke;
            border-radius: 10px;
            box-shadow: 0 1px 2px #000000;
        }
    }

    .newest{
        li:nth-child(4){
            position: relative;
            background-color: rgb(0, 0, 0);
            color:whitesmoke;
            border-radius: 10px;
            box-shadow: 0 1px 2px #000000;
        }
    }

    .cheapest{
        li:nth-child(5){
            position: relative;
            background-color: rgb(0, 0, 0);
            color:whitesmoke;
            border-radius: 10px;
            box-shadow: 0 1px 2px #000000;
        }
    }

    .mostExpensive{
        li:nth-child(6){
            position: relative;
            background-color: rgb(0, 0, 0);
            color:whitesmoke;
            border-radius: 10px;
            box-shadow: 0 1px 2px #000000;
        }
    }
}