.productInfoView{
    direction: rtl;
    text-align: justify;
    text-justify: inter-word;
    line-height: 20px;

    .MuiTab-root{
        font-family: 'IRANYekan';
        font-size: 16px;

        &.Mui-selected{
            color:rgb(57, 18, 18);
        }
    }

    .MuiTabs-indicator{
        background-color: rgb(254, 212, 212);
    }


    p{
        line-height: 36px;
    }


    p,h4,h6,span,label{
        font-family: 'IRANYekan';
    }
}