.account{
    margin:20px 20px 15px 20px !important;
    position: relative;
    max-width: 200px !important;
    padding-bottom:5px;

    @include media-breakpoint-down(md) {
        margin:0 5px !important;
    }

    @include media-breakpoint-down(xs) {
        margin:0 !important;
    }

    p,span,h6,h5{
        font-family: 'IRANYekan';
        color:whitesmoke;
    }
    .accountBox{
        justify-content: space-around;
        align-items: center;
        border: 1px solid #ccc8c8;
        height:40px;
        border-radius: 4px;
        p{
            color:#161616;
        }
        cursor: pointer;
        
        &:hover{border: 1px solid #161616;}
        
        @include media-breakpoint-down(xs) {
            padding:0 20px;
            justify-content: flex-start;
            z-index: 10;
            cursor: default;
        }
    }

    .accountModal{
        position: absolute;
        top: 45px;
        min-width: 0px;
        height: 0;
        z-index: 10;
        background-color: rgba(79, 79, 79,0.9);
        box-sizing: border-box;
        // padding: 5px;
        border-radius: 6px;
        box-shadow: 0 1px 3px rgb(36, 36, 36);
        transition: all .3s cubic-bezier(0.075, 0.82, 0.165, 1);
        overflow:hidden;

        @include media-breakpoint-down(xs) {
            position: fixed;
            top:0;
            left:0;

        }

        form{
            position: relative;
            display: flex;
            flex-direction: column;
            min-width:100%;
            height:180px;

            .showPass{
                position: absolute;
                right:25px;
                width:20px;
                height:20px;
                color:#cdcdcd;
                cursor: pointer;
                transition: color 0.5s;
                &:hover{
                    color:rgb(97, 97, 97);
                }

                @include media-breakpoint-down(xs) {
                    right: 45px;
                }
            }

            .loginError{
                color:rgb(255, 204, 204);
                padding: 5px 20px;
            }

            .forgetPass{
                color:rgb(237, 237, 237);
                padding: 5px 10px;
                cursor: pointer;
                &:hover{
                    color:rgb(191, 191, 191);
                }
            }

            .inputBox{
                justify-content: center;
                align-items: center;
                min-width: 100%;
                height: 30px;
            }

            input{
                width: 90%;
                height: 30px;
                margin: 0 5%;
                padding:0 10px;
                padding-top: 4px;
                border-radius: 6px;
                box-shadow: 0 1px 3px #999 inset,0 -1px 3px #999 inset;
                direction: ltr;
                font-size: 12px;
            }

            .label{
                justify-content: flex-start;
                align-items: center;
                padding-right: 20px;
                min-width: 100%;
                height: 30px;
            }

            .button{
                background-color: rgb(255, 247, 252);
                width:100%;
                height: 30px;
                transition: background .5s;
                p{color:rgb(0, 0, 0);}
                &:hover{
                    background-color: rgb(194, 194, 194);
                }
    
            }
        }
    }

    .showAccount{
        height: 210px;
        min-width:300px;

        @include media-breakpoint-down(xs) {
            padding : 0 20px;
            min-width: 100vw;
        }
    }

    .hideAccount{
        height:0px;
        min-width:0px;
        width: 0;
    }

    .removeBorder{
        border: none;
        &:hover{border:none;}
    }
    .accountIcon{
        width: 25px;
        height: 25px;
        &:hover{
            color: #6f6f6f !important;
            cursor: pointer;
        }
    }
}

.registerContainer{
    position: relative;
    width: 100%;
    min-height:80vh;
    justify-content: center;
    align-items: flex-start;
    top: 50px;

.blurEffect{
    position:fixed;
    top:120px;
    left:0;
    justify-content: center;
    align-items: center;
    width:100vw;
    height: 70vh;
    background-color: rgba(215, 215, 215, 0.5);
    backdrop-filter: blur(6px);
    background-position: left center;
    background-repeat: no-repeat;
    background-size: cover;
    filter: blur(2px);
    
   
}
  

.register{
    width: 50vw;
    height: 100%;
    direction: rtl;

    p,span,h6,h5{
        font-family: 'IRANYekan';
        color:whitesmoke;
    }
    .accountModal{
        position: relative;
        top:50px;
        height: 400px;
        max-height: 450px;
        background-color: rgb(79, 79, 79);
        box-sizing: border-box;
        padding: 15px;
        border-radius: 6px;
        box-shadow: 0 1px 3px rgb(36, 36, 36);
        transition: all .3s cubic-bezier(0.075, 0.82, 0.165, 1);
        overflow:hidden;

        .header{
            border-bottom: 1px solid #9f9f9f;
            padding: 5px 0 10px 0;

        }

        form{
            position: relative;
            display: flex;
            flex-direction: column;
            min-width:100%;
            height: 400px;
            max-height: 450px;

            .showPass{
                position: absolute;
                // top:10px;
                right:40px;
                width:20px;
                height:20px;
                color:#cdcdcd;
                cursor: pointer;
                transition: color 0.5s;
                &:hover{
                    color:rgb(97, 97, 97);
                }
            }

            .loginError{
                
                padding: 5px 20px;
                max-height: 30px;
                span{
                    color:rgb(255, 170, 170) !important;
                }
            }

            .forgetPass{
                color:black;
                padding: 5px 10px;
                cursor: pointer;
                &:hover{
                    color:rgb(91, 91, 91);
                }
            }

            .inputBox{
                position: relative;
                justify-content: center;
                align-items: center;
                min-width: 100%;
                height: 30px;
                max-height: 50px;
            }

            input{
                display: block;
                position:relative;
                float:left;
                width: 90%;
                height: 30px;
                margin: 0 5%;
                padding:0 10px;
                padding-top: 4px;
                border-radius: 6px;
                box-shadow: 0 1px 3px #999 inset,0 -1px 3px #999 inset;
