                font-size: 16px;
            }

            .label{
                justify-content: flex-start;
                align-items: center;
                padding-right: 20px;
                min-width: 100%;
                height: 30px;
                max-height: 30px;
            
                p{
                    font-family: 'IRANYekan';
                }
                    
            }

            .button{
                position:relative;
                background-color: rgb(255, 227, 243);
                width:100%;
                height: 30px;
                transition: background .5s;
                p{color:rgb(0, 0, 0);}
                &:hover{
                    background-color: rgb(194, 194, 194);
                }
    
            }
        }
    }
    }
    
}