.productCart{
    // flex-grow: 1;
    min-width: 200px;
    min-height: 200px;
    border-radius: 8px;
    box-shadow: 0 1px 3px #000000;
    // max-width: 200px;
    cursor: pointer;

    p,span,h1,h2,h3,h4,h5,h6{
        font-family: 'IRANYekan';
    }
    
    &.animation{
        
        transition: all 0.4s;
        &:hover{
            transform: scale(1.05);
        }
    }

    .productImg{
        justify-content: center;
        align-items: center;
        background-color: rgb(255, 255, 255);
        border-radius: 8px 8px 0 0;
        padding: 5px;
        box-sizing: border-box;

        img{
            // min-width:70%;
            // height:auto;
            // border-radius: 8px 8px 0 0;
            object-fit: contain;
            max-height: 100%;
        }
    }
    .productInfo{
        min-height: 110px;
        background-color: #ffffff;
        padding: 5px 10px;
        box-sizing: border-box;
        justify-content: center;
        align-items: center;
        border-radius: 0 0 8px 8px;
        direction: rtl;
        background-color: rgb(255, 255, 255);

        .productTitle{
            justify-content: flex-start;
            align-items: center;
            // text-overflow:ellipsis;
            // white-space: nowrap;
            // overflow: hidden;
            p{
                text-overflow: ellipsis;
                white-space: nowrap;
                overflow: hidden;
                width:160px;
            }
        }
        .productDesc{
            justify-content: center;
            align-items: center;
        }
        .productDiscount{
            justify-content: center;
            align-items: center;
            justify-content: center;
            direction: rtl;
           

            .discount{
                background-color: rgb(231, 85, 109);
                padding:5px 10px;
                border-radius: 15px;
                color:white;
                margin-right:20px;
                font-size: 20px;
            }

            .oldPrice{
                position: relative;
            }

            .oldPrice::before{
                content: '';
                width: 100%;
                position: absolute;
                right: 0;
                top: 50%;
                border-bottom: 1px solid rgba(255,0,0,0.8);
        }
        }
        .productPrice{
            justify-content: center;
            align-items: center;
            direction: rtl;
        }
    }
}