
.nav{
    // background-color: rgb(255, 255, 255);
    // box-shadow:1px 2px 3px #000000;
    // border-radius: 0 0 8px 8px;
    padding:0 200px;

    @include media-breakpoint-down(md) {
        padding: 0 50px;
    }

    p{
        font-family: 'IRANYekan';
        color:rgba(0, 0, 0, 1);
        &:hover{
            color:rgba(180, 25, 25, 0.7);
        }

        @include media-breakpoint-down(xs) {
            color:azure;
        }
    }

    a{
        text-decoration: none;
    }

    .menuItem{
        display: flex;
        justify-content: center;
        align-items: center;
        color:rgb(0, 0, 0);
        box-sizing: border-box;
        height:40px;
    }

   
}

.navMobile{
    width: 100%;
    height: auto;
    min-height:calc(100vh - 120px);
    max-height: 100%;
    position: absolute;
    left:0;
    bottom: 45px;
    z-index: -1;
    overflow-y: auto;
    transition: all 0.5s ease;
    transition-property: min-height, height;
    box-shadow: 0 -1 2 #000000;
    border-radius: 5px 5px 0 0;
    background-color: rgba(0, 0, 0, 0.6);

    .menuItem{
        flex: 1 1 100%;
        min-width: 100%;
        color:white;
        text-overflow: clip;
    }
}

.closeNav{
    min-height:0;
    height: 0;
    overflow: hidden;
}

