.tag{
    position: relative;
    display: flex;
    border:1px solid black;
    flex-basis: content;
    // flex-grow: 0.1;
    flex: 0.3 1 auto;
    justify-content: space-between;
    align-items: center;
    direction: rtl;
    padding:5px;
    max-height: 40px;
    box-sizing: border-box;
    border-radius: 10px;
    background-color: white;
    margin:2px !important;

    span{
        display: flex;
    }

    svg:hover{
        color:brown;
        cursor: pointer;
    }
}