.season{
    position: relative;
    padding:20px 5%;
    justify-content: flex-end;
    background-repeat: no-repeat;
    background-position: 0% center;
    background-color: rgb(131,188,199);
    background-size: 60%;
    background-attachment: fixed;
    box-shadow: 0 1px 2px #111111,0 -1px 2px #111111;
    color:white;

    @include media-breakpoint-down(xs) {
        h6{
            font-size: 3vw;
        }
        

    }
}