.footer{
    margin : 0 !important;
    padding:0 !important;
    position: relative;
    width: 100%;
    justify-content: center;
    align-items: center;
    background-color: rgb(248, 248, 248);
    min-height: 50px;
    padding-top:20px;
    p,span,h1,h2,h3,h4,h5,h6{
        font-family: 'IRANYekan';
    }

    .socialIcon{
        width:30px !important;
        height: 30px !important;
        margin:10px;
        transition: transform 0.3s ease-out;

        &:hover{
            transform: rotate(-360deg);
        }
    }
    

    .leftBox{
        min-height: 150px;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        svg{
            width:50px;
            height:50px;
        }
    }

    .centerBox{
        min-height: 200px;

        img{
            width:100%;
            height:auto;

            @include media-breakpoint-down(sm) {
                width: 80%;
            }
        }
    }

    .rightBox{
        min-height: 150px;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        svg{
            width:50px;
            height:50px;
        }
    }

    @include media-breakpoint-down(sm) {
        margin-bottom: 70px;
    }

    .bottom{
        min-height: 300px;
        background-color: rgb(255, 255, 255);
        padding : 0 50px;
        box-sizing: border-box;
        border-radius: 30% 30% 0 0;

        @include media-breakpoint-down(sm) {
            flex-direction: row-reverse;
        }

        @include media-breakpoint-down(xs) {
            padding : 0 20px 50px 20px;
        }

        .subscriber{
            input{
                max-width:80%;
            }
            button{
               max-width:20%;
            }
        }

        .footerText{
            p,a{
                color: #4d4d4d;
                text-align: left;
            }
            span{
                color: #000000;
            }

            @include media-breakpoint-down(xs) {
                justify-content: center;
                align-items: center;
            }
        }

        input{
            width:80%;
            max-height: 40px;
            border:1px solid #4d4d4d;
            box-shadow: 0px 1px 3px #333;
            border-radius: 0 4px 4px 0px;
            padding: 5px 15px;

            &:focus{
                border: none;
                outline: none;
            }
        }

        .button{
            background-color: rgb(55, 55, 55);
            width:20%;
            max-height: 40px;
            transition: background .5s;
            p{color:white;transition: color .5s;}
            &:hover{
                background-color: rgb(255, 155, 155);
                p{color:rgb(43, 43, 43)}
            }

        }

        .enamad{
            width: 200px;
            height:auto;
            img{
                width:100%;
                height:auto;
            }
        }
    }
}