.cartViewContainer{
    position: relative;
    justify-content: flex-start;
    align-items: center;
    box-shadow: 0px 1px 2px #777;
    padding:0 10px 10px 10px;
    box-sizing: border-box;
    flex-direction: row;

    p,h6,span{
        font-family: 'IRANYekan';
    }

    .header{
        padding:10px;

        span{
            font-size: 14px;
            color:rgb(84, 84, 84);
        }
    }

    .title{
        justify-content: flex-end;
        align-items: center;
    }

    .cartItem{
        position: relative;
        box-shadow: 0px 1px 2px #bbb;
        margin: 5px;
        border-radius: 6px;
        flex-direction: row-reverse;
        max-height: 200px;
        .remove{
            position: absolute;
            left: 5px;
            top: 5px;
            width: 20px;
            height:20px;
            color:rgb(255, 126, 126);
            cursor: pointer;
            transition: color .5s;
            &:hover{
                color:black;
            }
        }

        .cartImage{
            max-width: 170px;
            padding:10px;
            box-sizing: border-box;
            img{
                width:150px;
                height:150px;
                cursor: pointer;
                border-radius: 6px;
            }
        }

        ul{
            list-style-type: none;
            display:flex;
            width:100%;
            flex-direction: row-reverse;
    
            .qtyButton{
                padding:1px 10px;
                background-color: rgb(216, 216, 216);
                color:white;
                border-radius: 50%;
                margin:0 5px;
                cursor: pointer;
                transition: background 0.5s;
                transition: color 0.5s;
    
                &:hover{
                    background-color: rgb(255, 182, 182);
                    color:black;
                }
            }
        
            li{
                display: flex;
                flex:1 1 auto;
                justify-content: center;
                align-items: center;
                &:first-child{
                    max-width: 200px;
                }
                &:nth-child(2){
                    max-width: 200px;
                }
                &:nth-child(3){
                    // max-width: 100px;
                }
            }
        }
    }

    .totalPart{
        position: sticky;
        top:5px;
        justify-content: flex-start;
        max-height:300px;
        margin:10px 0;
        padding:10px;
        box-shadow: 0 1px 3px #333;
        direction: rtl;

        span{
            font-size: 12px;
        }

        h6,p,span{
        color:rgb(58, 58, 58);
        }

        .button{
            background-color: rgb(255, 130, 130);
            width:100%;
            max-height: 40px;
            transition: background .5s;
            p{color:white;transition: color .5s;}
            &:hover{
                background-color: rgb(255, 155, 155);
                p{color:rgb(43, 43, 43)}
            }

        }
    }


}