@mixin line-clamp($numLines : 1, $lineHeight: 1.412) {
    overflow: hidden;
    text-overflow: -o-ellipsis-lastline;
    text-overflow: ellipsis;
    display: block;
    /* autoprefixer: off */
    display: -webkit-box;
    -webkit-line-clamp: $numLines;
    -webkit-box-orient: vertical;
    max-height: $numLines * $lineHeight + unquote('em');
  }
.mainDescription{
    position: relative;
    justify-content: center;
    align-items: center;
    padding: 5%;
    max-height: 500px;
    cursor: pointer;

    p,h6{
        font-family: 'IRANYekan';
    }

    p{
        color:#ccc;
    }

    @include media-breakpoint-down(sm) {
        margin: 10px 0;

    }
    
    .backgroundRect{
        width: 30%;
        height: 100%;
        background-color: rgb(61, 90, 72);
        position: absolute;
        left:0;
        top:0;
        z-index:0;
        box-shadow: 1px 2px 2px #222;
    }

    .description{
        position:absolute;
        bottom: 0;
        width:90%;
        height:45%;
        background-color: rgba(0,0,0,0.6);
        padding:20px;
        margin:5%;
        direction: rtl;
        color:white;
        z-index:2;
        border-radius: 0 0 0 4px;
        transition: all  ease-out 0.5s;
        transition-property: background-color, height;

        &:hover{
            background-color: rgba(0, 0, 0, 0.9);
            height:75%;
            transition: all cubic-bezier(0.175, 0.885, 0.32, 1.275) 0.8s;
            transition-property: background-color, height;
        }

        @include media-breakpoint-down(md) {
            padding:10px;
            h6{
                font-size: 16px;
            }

            p{
                position: relative;
                display: block;
                width: 100%;
                height:100px;
                @include line-clamp(2,1.4 );
            }
        }

    }

    .image{
        width:100%;
        height:auto;
        z-index:1;
        border-radius: 4px;
        max-height: 400px;

        img{
            width:100%;
            height:auto;
            border-radius: 4px;
            max-height: 400px;
        }

        @include media-breakpoint-down(md) {
            img{
                width:100%;
            }
        }

    }
}