@import './components/Account/index.scss';
@import './components/Cart/index.scss';
@import './components/Lang/index.scss';
@import './components/Logo/index.scss';
@import './components/Nav/index.scss';
@import './components/Search/index.scss';

.header{
    direction: rtl;

    .headerMobileView{
        // background-color: rgb(159, 207, 163);
        background-color: white;
        box-shadow: 0px 1px 3px #000000;
        border-radius:0 0 6px 6px;
        z-index: 10;
        position: fixed;
        top:0;
        left:0;
    }
    .bottomMenu{
        position: fixed;
        bottom: 0;
        left:0;
        height: 60px;
        // padding-bottom: 5px;
        z-index: 10;

        .wave{
            position: absolute;
            bottom:0;
            left:0;
        }
        >svg{
            filter: drop-shadow(0px -1px 3px #333);
        }
        
        svg{
            color:black;
            &:hover{
                color:white;
            }
        }
    }
}
