@import './components/filteredItems/index.scss';
@import './components/productsCategorization/index.scss';
@import './components/radioBtn/index.scss';
@import './components/tag/index.scss';
@import './components/category/index.scss';
@import './components/price/index.scss';
@import './components/specialOffer/index.scss';

.filterGroup{
    height: fit-content;
    justify-content: center;
    padding: 0 10px 0 0;
    box-sizing: border-box;
    position: sticky;
    top:0;
    max-height: 100vh;
    overflow:auto;
    direction : rtl;

    .closeFilter{
        height: 40px;
        align-items: center;
        padding:0 15px;
        box-sizing: border-box;
        box-shadow: 0 1px 3px #111;
        border-radius: 0 0 10px 10px;
        background-color: rgb(255, 194, 194);

        svg{
            font-size: 24px;
            &:hover{
                cursor: pointer;
                color:#969696;
            }
        }
    }
     
    @include media-breakpoint-down(sm) {
        background-color: burlywood;
        max-height: 100vh;
    }


    &::-webkit-scrollbar {
        width: 20px;
    }
    &::-webkit-scrollbar-track {
    background-color: transparent;
    }
    &::-webkit-scrollbar-thumb {
    background-color: #3b3e41;
    border-radius: 20px;
    border: 6px solid transparent;
    background-clip: content-box;
    }

    &::-webkit-scrollbar-thumb:hover {
        background-color: #000000;
      }

    .rotateDown{
        cursor: pointer;
            transform: rotate(0deg);
            transition: all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    }

    .rotateUp{
        cursor: pointer;
            transform: rotate(180deg);
            transition: all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);

    }
}