.cart{
    margin:20px 20px 15px 20px !important;
    position: relative;
    max-width: 200px !important;
    padding-bottom:5px;

    @include media-breakpoint-down(md) {
        margin: 0 0 0 30px !important;
    }

    @include media-breakpoint-down(xs) {
        margin: 0 !important;
    }

    p{
        font-family: 'IRANYekan';
    }
    .cartBox{
        align-items: center;
        border: 1px solid #ccc8c8;
        height:40px;
        border-radius: 4px;
        cursor: pointer;
        &:hover{border: 1px solid #161616;}
        @include media-breakpoint-down(xs) {
            justify-content: center;
            flex-direction: row-reverse;
            cursor: default;

        }
        
    }

    .removeBorder{
        border:none;
        &:hover{border:none;}
    }

    .icon{
        width: 25px;
        height: 25px;

        &:hover{
            cursor: pointer;
        }
    }

    .quantity{
        background-color: rgba(79, 79, 79,0.9);
        border-radius: 50%;
        color:rgb(43, 43, 43);
        padding:2px 10px;
        box-sizing: border-box;

        @include media-breakpoint-down(xs) {
            display: flex;
            width:30px;
            height:30px;
            justify-content: center;
            align-items: center;
            
        }
    }

    .cartView{
        position: absolute;
        top: 45px;
        justify-content: center;
        align-items: center;
        width: 0px;
        box-sizing: border-box;
        background-color: rgba(79, 79, 79,0.96);
        border-radius: 6px;
        box-shadow: 0px 1px 2px #333;
        z-index : 10;
        overflow-y: auto;
        transition: all .3s cubic-bezier(0.075, 0.82, 0.165, 1);
        overflow-x:hidden;

        .closeCart{
            position: absolute;
            display: flex;
            justify-content: center;
            align-items: center;
            cursor: pointer;
            left:5px;
            top:5px;
            width:30px;
            height:30px;

            .remove{
                color:white;
                &:hover{
                    color:rgb(191, 191, 191);
                }
                
            }

        }

        @include media-breakpoint-down(xs) {
            position: fixed;
            top:0;
            left:0;
        }

        @include media-breakpoint-down(md) {
            position: fixed;
            top:65px;
            left:0;
        }

        p,span,h6,h5{
            font-family: 'IRANYekan';
            color:rgba(255,255,255,1);
        }

        .button{
            background-color: rgba(255, 223, 223,1);
            width:100%;
            transition: background .5s;
            p{color:rgba(45, 45, 45,1);transition: color .5s;}
            &:hover{
                background-color: rgba(194, 194, 194,1);
                p{color:rgba(58, 58, 58,1)}
            }

        }

        ul{
            list-style-type: none;
            display:flex;
            width:100%;
            height: 30px;

            .remove{
                color:rgb(255, 126, 126);
                cursor: pointer;
                transition: color .5s;
                &:hover{
                    color:rgb(235, 235, 235);
                }
            }

            li{
                display: flex;
                flex:1 1 auto;
                justify-content: center;
                align-items: center;
                &:first-child{
                    max-width: 20px;
                }
                &:nth-child(2){
                    max-width: 50px;
                }
                &:nth-child(3){
                    max-width: 100px;
                    p{
                        text-overflow: ellipsis;
                        white-space: nowrap;
                        overflow: hidden;
                    }
                }
                &:nth-child(4){
                    max-width: 50px;
                }
                &:nth-child(5){
                    max-width: 70px;
                }
            }
        }
    }

    .showCart{
        min-width:400px;
        max-height: calc(100vh - 65px);

        @include media-breakpoint-down(xs) {
            min-width:98vw;

        }
    }

    .hideCart{
        min-width:0px;
        max-height: 0;
        width: 0;
    }
}