.productsGroup{
    padding: 10px 0;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    min-height: 300px;


    .firstPart{
        display: flex;
        justify-content: center;
        align-items: center;
        min-width: 34%;
        @include media-breakpoint-down(md) {
            min-width: 50%;
            flex-wrap: wrap;
        }
    }

    .secondPart{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: row;
        flex-wrap: wrap;
        min-width: 66%;
        column-gap: 10px;
        @include media-breakpoint-down(md) {
            min-width: 50%;
        }
    }
    
}